import { IconButton, TextField, Typography } from "@material-ui/core";
import "./Screen.css";
import React, { useEffect, useState } from "react";
import { Add } from "@material-ui/icons";
import * as firebase from "firebase";
import { toast } from "react-toastify";
const Section3 = (props) => {
  const [state, setState] = useState({
    about: "",
    call_tracking_phone_number: "",
    UTM_tracking_website_URL: "",
    bing_call_tracking_phone_number: "",
    bing_UTM_tracking_website_URL: "",
    cover_photo: null,
    bing_category: "",
    additional_bing_categories: "",
    apple_category: "",
    additional_apple_categories: "",
    apple_coordinates_lat: null,
    apple_coordinates_long: null,
  });

  const [auth, setAuth] = useState();

  useEffect(() => {
    firebase.default.auth().onAuthStateChanged((user) => {
      if (!user) {
        props.history.push("/");
      } else {
        setAuth(user);
      }
    });
  });

  const getBase64 = (file, cb) => {
    let reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      cb(reader.result);
    };
    reader.onerror = function (error) {
      console.log("Error: ", error);
    };
  };

  const handleChange = (event) => {
    const name = event.target.name;
    console.log(name);
    setState({
      ...state,
      [name]: event.target.value,
    });
  };

  const form = [
    {
      label: <Typography>About</Typography>,
      component: (
        <TextField
          variant="filled"
          size="small"
          placeholder="A description of your business for the Facebook About Us section of a listing. 155 characters max"
          multiline
          value={state.about}
          onChange={handleChange}
          inputProps={{
            name: "about",
            id: "outlined-about-native-simple",
          }}
        />
      ),
    },
    {
      label: <Typography>Call Tracking Phone Number</Typography>,
      component: (
        <TextField
          variant="filled"
          size="small"
          placeholder="Tracking Phone Number"
          value={state.call_tracking_phone_number}
          onChange={handleChange}
          inputProps={{
            name: "call_tracking_phone_number",
            id: "outlined-call_tracking_phone_number-native-simple",
          }}
        />
      ),
    },
    {
      label: <Typography>UTM/Tracking Website URL</Typography>,
      component: (
        <TextField
          variant="filled"
          size="small"
          placeholder="Tracking URL"
          value={state.UTM_tracking_website_URL}
          onChange={handleChange}
          inputProps={{
            name: "UTM_tracking_website_URL",
            id: "outlined-Primary-Website-native-simple",
          }}
        />
      ),
    },
    {
      label: <Typography>Cover Photo</Typography>,
      component: (
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            borderWidth: 1,
            borderColor: "#9e9e9e",
            borderStyle: "solid",
          }}
        >
          <div style={{ margin: 20, width: "93%", flexDirection: "row" }}>
            <input
              accept="image/*"
              style={{ display: "none" }}
              id="icon-button-file2"
              type="file"
              onChange={(e) => {
                if (e.target.files[0]) {
                  getBase64(e.target.files[0], (result) => {
                    setState({ ...state, cover_photo: result });
                  });
                }
              }}
            />
            <label
              htmlFor="icon-button-file2"
              style={{ display: "flex", justifyContent: "center" }}
            >
              {state.cover_photo === null ? (
                <IconButton
                  color="primary"
                  aria-label="upload picture"
                  component="span"
                >
                  <Add
                    style={{
                      backgroundColor: "#ff9100",
                      color: "#fff",
                      borderRadius: 25,
                      borderWidth: 2,
                      borderColor: "#000",
                      borderStyle: "solid",
                    }}
                  />
                </IconButton>
              ) : (
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                  }}
                  onClick={() => {
                    setState({ ...state, cover_photo: null });
                  }}
                >
                  <img
                    src={state.cover_photo}
                    alt="Cover_Photo"
                    style={{ width: 180, height: 150 }}
                  />
                  <Typography
                    variant="caption"
                    style={{
                      color: "#2196f3",
                      cursor: "pointer",
                      fontSize: 18,
                      textDecoration: "underline",
                    }}
                  >
                    Remove
                  </Typography>
                </div>
              )}
            </label>
          </div>
        </div>
      ),
    },
  ];

  const form2 = [
    {
      label: (
        <Typography>
          Bing Category
          <span
            style={{
              color: "#ff1744",
              marginRight: 30,
            }}
          >
            *
          </span>
        </Typography>
      ),
      component: (
        <TextField
          variant="filled"
          size="small"
          placeholder="Internet Marketing Agency"
          value={state.bing_category}
          onChange={handleChange}
          inputProps={{
            name: "bing_category",
            id: "outlined-bing_category-native-simple",
          }}
        />
      ),
    },
    {
      label: (
        <Typography>
          Additional Bing Categories
          <span
            style={{
              color: "#ff1744",
              marginRight: 30,
            }}
          >
            *
          </span>
        </Typography>
      ),
      component: (
        <TextField
          variant="filled"
          size="small"
          placeholder="Advertising Agency"
          value={state.additional_bing_categories}
          onChange={handleChange}
          inputProps={{
            name: "additional_bing_categories",
            id: "outlined-additional_bing_categories-native-simple",
          }}
        />
      ),
    },
    {
      label: <Typography>Call Tracking Phone Number</Typography>,
      component: (
        <TextField
          variant="filled"
          size="small"
          placeholder="Call Tracking Phone Number"
          value={state.bing_call_tracking_phone_number}
          onChange={handleChange}
          inputProps={{
            name: "bing_call_tracking_phone_number",
            id: "outlined-call_tracking_phone_number-native-simple",
          }}
        />
      ),
    },
    {
      label: <Typography>UTM/Tracking Website URL</Typography>,
      component: (
        <TextField
          variant="filled"
          size="small"
          placeholder="UTM/Tracking Website URL"
          value={state.bing_UTM_tracking_website_URL}
          onChange={handleChange}
          inputProps={{
            name: "bing_UTM_tracking_website_URL",
            id: "outlined-Primary-Website-native-simple",
          }}
        />
      ),
    },
  ];

  const user = [
    {
      label: (
        <Typography>
          Apple Category
          <span
            style={{
              color: "#ff1744",
              marginRight: 30,
            }}
          >
            *
          </span>
        </Typography>
      ),
      component: (
        <TextField
          variant="filled"
          size="small"
          placeholder="Advertising"
          value={state.apple_category}
          onChange={handleChange}
          inputProps={{
            name: "apple_category",
            id: "outlined-bing_category-native-simple",
          }}
        />
      ),
    },
    {
      label: (
        <Typography>
          Apple Additional Categories
          <span
            style={{
              color: "#ff1744",
              marginRight: 30,
            }}
          >
            *
          </span>
        </Typography>
      ),
      component: (
        <TextField
          variant="filled"
          size="small"
          placeholder="Search."
          value={state.additional_apple__categories}
          onChange={handleChange}
          inputProps={{
            name: "additional_apple_categories",
            id: "outlined-additional_bing_categories-native-simple",
          }}
        />
      ),
    },
    {
      label: <Typography>Apple Coordinates</Typography>,
      component: (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-evenly",
          }}
        >
          <TextField
            variant="filled"
            style={{ marginBottom: 10 }}
            size="small"
            placeholder="Latitude"
            value={state.apple_coordinates_lat}
            onChange={handleChange}
            inputProps={{
              name: "apple_coordinates_lat",
              id: "outlined-apple_coordinates.lat-native-simple",
            }}
          />
          <TextField
            variant="filled"
            size="small"
            placeholder="Longitude"
            value={state.apple_coordinates_long}
            onChange={handleChange}
            inputProps={{
              name: "apple_coordinates_long",
              id: "outlined-apple_coordinates.long-native-simple",
            }}
          />
        </div>
      ),
    },
  ];
  return (
    <div
      style={{
        flex: 1,
        margin: 25,
        padding: 25,
      }}
    >
      <div style={{ display: "flex", justifyContent: "center" }}>
        <div
          style={{
            borderColor: "#3d5afe",
            borderWidth: 1,
            borderStyle: "solid",
            background: "#bbdefb",
            borderRadius: 7,
            padding: 10,
          }}
        >
          <Typography align="center" style={{ width: "100%", height: 20 }}>
            Let's Get Started
          </Typography>
        </div>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "flex-end",
        }}
      >
        <Typography>
          <span
            style={{
              color: "#ff1744",
              marginRight: 30,
            }}
          >
            *
          </span>
          Required Field
        </Typography>
      </div>
      <div
        style={{
          display: "flex",
          flexWrap: "wrap",
          flexDirection: "row",
          justifyContent: "space-evenly",
        }}
      >
        <div
          style={{
            justifyContent: "center",
            display: "flex",
          }}
        >
          <div>
            {form.map((item, index) => (
              <div
                key={index}
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  margin: 20,
                  flexWrap: "wrap",
                }}
              >
                <div style={{ alignSelf: "flex-start" }}>{item.label}</div>
                {item.component}
              </div>
            ))}
          </div>
        </div>
        <div
          className={"R1"}
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            alignContent: "center",
          }}
        >
          <img
            src="/right-arrow.png"
            alt="next"
            style={{ width: 80, height: 50 }}
          />
        </div>
        <div
          style={{
            justifyContent: "center",
            display: "flex",
          }}
        >
          <div>
            {form2.map((item, index) => (
              <div
                key={index}
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  margin: 20,
                  flexWrap: "wrap",
                }}
              >
                <div style={{ alignSelf: "flex-start" }}>{item.label}</div>
                {item.component}
              </div>
            ))}
          </div>
        </div>
        <div
          className={"R1"}
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            alignContent: "center",
          }}
          onClick={() => {
            console.log(state);
          }}
        >
          <img
            src="/right-arrow.png"
            alt="next"
            style={{ width: 80, height: 50 }}
          />
        </div>
        <div
          style={{
            justifyContent: "flex-start",
            display: "flex",
          }}
        >
          <div>
            {user.map((item, index) => (
              <div
                key={index}
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  margin: 20,
                  flexWrap: "wrap",
                }}
              >
                <div
                  style={{
                    flexGrow: 1,
                    alignSelf: "flex-start",
                  }}
                >
                  {item.label}
                </div>
                {item.component}
              </div>
            ))}
          </div>
        </div>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "flex-end",
        }}
        onClick={() => {
          firebase.default
            .firestore()
            .collection(auth.uid)
            .doc("section3")
            .set(state)
            .then((res) => {
              toast.success("Section added");
              props.history.push("/business_description");
            });
        }}
      >
        <img
          src="/right-arrow.png"
          alt="next"
          style={{ width: 75, height: 45 }}
        />
      </div>
    </div>
  );
};

export default Section3;
